@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i,800,800i);
@import url(https://fonts.googleapis.com/css?family=Racing+Sans+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
}
.App {
   max-width: 30em;
   margin: 0 auto;
   height: 100vh;
   display: flex;
   flex-direction: column;
   font-family: 'Open Sans', sans-serif;
 }

 .App > * {
   flex: 1 1 auto;
 }
.header {
  background-color: #FFA45B;
  display: flex;
  justify-content: center;
  padding: 0.5em;
  max-height: 4em;
  min-height: 3em;
  color: #FBF6F0;
  align-items: center;  
}

.header h1 {
  font-size: 2.8em;
  font-family: 'Racing Sans One', cursive;
}
.kulukortti {
 /*  padding: 1em 0em 1em 1em; */
   border-bottom: 1px solid #ECE7E5;
   display: flex;
}
 
.kulukortti__rivi {
   display: flex;
   justify-content: space-between;
}
 
.kulukortti__tyyppi, .kulukortti__summa {
   font-weight: bold;
}
 
.kulukortti__kulupaiva, .kulukortti__ajanjakso,
.kulukortti__laskuttaja, .kulukortti__keskiarvo, .kulukortti__kilometrit {
   font-size: 85%;
   color: #666;
}

.kulukortti > div {
   flex: 1 1 auto;
   padding: 1em;
}

.kulukortti__linkki {
   max-width: 0.7em;
   display: none;
   justify-content: center;
   align-items: center;
   background-color: rgba(0,0,0,0.1);
}

.kulukortti:hover .kulukortti__linkki {
   display: flex; 
}

.kulukortti__linkki a {
   color: #000;
}
.content {
  overflow-y: scroll;
  -webkit-overflow-scrolling: touch;  
  position: relative;
}

::-webkit-scrollbar {
  width: 0.5em;
}

::-webkit-scrollbar-track {
  background-color: rgba(0, 0, 0, 0.0);
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, 0.1);
}
.button {
    background-color: #ccc;
    color: #000;
    border: none;
    outline: none;
    padding: 0.7em;
    margin: 0.5em;
    border-radius: 0.2em;
    box-shadow: rgba(0,0,0,0.5) 0px 2px 2px;
    font-weight: bold;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    opacity: 0.8;
}

.button:active {
    box-shadow: rgba(0,0,0,0.5) 0px 2px 5px;
    background-color: #999;
}

.button:hover {
    opacity: 1;
}

.button--primary {
    background-color: #65626B;
    color: #fff;
}

.button--primary:active {
    background-color: #ffa45b;
}

.button--secondary {
    background-color: #FFDA77;
    color: #fff;    
}

.button--secondary:active {
    background-color: #ff9b3e;
}

.button:disabled {
    background-color: #ccc;
    color: #999;
    box-shadow: none;
}

.button--floating {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
    border-radius: 50%;
    font-size: 3rem;
    width: 1em;
    height: 1em;
}
.settings {
    padding: 2em;
}

.settings h3 {
    margin-top: 0.6em; 
}

.settingsForm {
    display: flex;
}

.settings__items {
    padding: 0.5em;
    font-size: 1.1rem;
}

.settings__items > div {
    padding: 0.2em;
    border-radius: 0.2em;
}

.settings__items > div:nth-child(odd) {
    background-color: rgba(0,0,0,0.1); 
}

.settingsForm input {
    margin-top: 0.5em;
    flex: 1 1 auto;
    padding: 0.5em;
    border: 1px solid #999;
    border-radius: 0.3em;
    font-size: 1.1rem;
    font-family: 'Nunito', sans-serif;
    box-sizing: border-box;   
    outline: none; 
}
.menu {
    background-color: #65626B;
    display: flex;
    justify-content: space-evenly;
    max-height: 4.5em;
    min-height: 4em;
    align-items: center;
  }
.itemform {
    display: flex;
    flex-direction: column;
}

.itemform__row {
    display: flex;
}

.itemform__row > div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin: 0.5em 0.3em;
    flex: 1 1 auto;    
}

.itemform input, .itemform select {
  padding: 0.3em;
  border: 1px solid #999;
  border-radius: 0.3em;
  font-size: 1em;
  font-family: 'Nunito', sans-serif;
  box-sizing: border-box;   
  outline: none;
  width: 100%;    
}

.itemform input[type="date"]::-webkit-clear-button {
    display: none;
}

.itemform input[type="date"]::-webkit-inner-spin-button {
    display: none;
}

.itemform label {
    font-size: 0.8em;
    margin-bottom: 0.2em;
}

.additem {
    padding: 2em;
}
.edititem {
    padding: 2em; 
}
